* {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  user-select: none;
}

#navbar {
  background-color: #080A12;
  height: 8vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
}
.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 700,
  'GRAD' -25,
  'opsz' 48
}


svg {
  height: 45px;
  margin: 2vh;
  background-color: #080A12;
}

.login-btn {
  height: 3.5vh;
  min-width: 5vw;
  margin: 2vh;
  border-radius: 5px;
  color: white;
  background-color: black;
  border: 2px solid white;
}

#slider {
  background-color: yellow;
  overflow: hidden;
  min-width: 98%;
  height: 50vh;
  margin: 1vw 1vw 0 1vw;
  border-radius:10px;

}

#slider img {
  width: 100%;
  height: 70vh;
}

#production-card {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width:100%;
  overflow: scroll;
}
#production-card::-webkit-scrollbar
{
  display: none;
}

.production-card-inner {
  /* background-color: blue; */
  margin: 2vh;
  height:20vh;
  min-width: 20vw;
  text-align: center;
  display: flex;
  color: white;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  /* background-color: blue; */
  box-shadow: 0px 2px 13px 2px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 2px 13px 2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 2px 13px 2px rgba(0, 0, 0, 0.75);
}

.movieCard {
  height: 28vh;
  min-width: 12vw;
  margin: 1vh;
  max-width: 19vw;
  border-radius: 7px;
  /* background-color: white; */
  position: relative;
}

.movie-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 28vh;
  width: 100%;
  height: 28vh;
  overflow-x: scroll;
  overflow-y: hidden;
}

.movie-div::-webkit-scrollbar {
  display: none;
}

div h1 {
  color: white;
  font-size: 20px;
  margin: 2vh;
}

.movieCard img {
  height:100%;
  width:100%;
  border-radius: inherit;
  object-fit: cover;
  border-radius: inherit;
  border:inherit;
  position:absolute;
  overflow: hidden;
}

.cards-pro {
  height: 15vh;
  width: 12vw;
  margin:auto;
}

.movieName {
  /* background-color: #0000ff; */
  position: absolute;
  background-color:rgba(54, 25, 25, .00004);;
  height: 100%;
  width: 100%;
  top: 0;
  display: flex;
  border-radius: inherit;
  justify-content: center;
  justify-content: center;
  align-items: center;
  color:white;
  display: none;
  font-weight: bolder;
}
.nav-btns
{
  color:white;
  opacity:0.7;
  cursor:pointer;
  user-select: none;
  background:none;
}
.arrows
{
  /* background-color:red; */
  background: none;
  position: absolute;
  display: flex;  
  justify-content: space-between;
  width:98%;
  top:33vh;
}
@media only screen and (max-width:600px) {
  .production-card-inner
  {
    height:15vh;
    min-width:40vw;
  }
  .production-card-inner img
  {
    height:fit-content;
  }
  .movieCard
  {
    /* background-color: white; */
    /* width:300px; */
    min-width:150px;
  }
  #slider
  {
    height:30vh;
  }
  #slider img
  {
    height:inherit;
  }
  .arrows
  {
    top:23vh;
  }
}